// Max Width Percentages

.mw-15 { max-width: 15%; }
.mw-50 { max-width: 50%; }
.mw-70 { max-width: 70%; }
.mw-100 { max-width: 100%; }

// Max Width Scale

.mw1 { max-width: 16px; }
.mw2 { max-width: 32px; }
.mw3 { max-width: 64px; }
.mw4 { max-width: 128px; }
.mw5 { max-width: 256px; }
.mw6 { max-width: 512px; }
.mw7 { max-width: 768px; }
.mw8 { max-width: 1024px; }
.mw9 { max-width: 1536px; }

// Max Width String Properties

.mw-none { max-width: none; }

