// FOCUS EFFECTS

// focus outline
.fc-o,
.fc-od {
    outline: 12px solid transparent;
    outline-offset: 2px;
}

.fc-o:focus:not(:active) {
    outline: 2px solid var(--color-primary-2);
}

.fc-od:focus:not(:active) {
    outline: 2px solid var(--color-gray-7);
}

.fc-s {
    box-shadow: 0 0 0 12px transparent;
}

.fc-s:focus:not(:active) {
    box-shadow: 0 0 0 2px var(--color-primary-1);
}

.fc-b:focus:not(:active) {
    border-color: var(--color-primary-4);
}

// focus outline none

.fc-on:focus {
  outline: none;
}

// Support for focus-visible
// Currently using Polyfill https://github.com/WICG/focus-visible
.js-focus-visible .fc-o:not(.focus-visible):focus { outline-color: transparent; }
.js-focus-visible .fc-od:not(.focus-visible):focus { outline-color: transparent; }
