.top-0 { top: 0; }
.right-0 { right: 0; }
.bottom-0 { bottom: 0; }
.left-0 { left: 0; }

.absolute--fill {
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
}

@media (--breakpoint-not-small) {
    .top-0-ns { top: 0; }
    .left-0-ns { left: 0; }
    .right-0-ns { right: 0; }
    .bottom-0-ns { bottom: 0; }

    .absolute--fill-ns {
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
    }
}

@media (--breakpoint-medium) {
    .top-0-m { top: 0; }
    .left-0-m { left: 0; }
    .right-0-m { right: 0; }
    .bottom-0-m { bottom: 0; }

    .absolute--fill-m {
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
    }
}

@media (--breakpoint-large) {
    .top-0-l { top: 0; }
    .left-0-l { left: 0; }
    .right-0-l { right: 0; }
    .bottom-0-l { bottom: 0; }

    .absolute--fill-l {
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
    }
}
