// DISPLAY

.dn { display: none; }
.di { display: inline; }
.db { display: block; }
.dib { display: inline-block; }

@media (--breakpoint-not-small) {
    .dn-ns { display: none; }
    .di-ns { display: inline; }
    .db-ns { display: block; }
    .dib-ns { display: inline-block; }
}

@media (--breakpoint-medium) {
    .dn-m { display: none; }
    .di-m { display: inline; }
    .db-m { display: block; }
    .dib-m { display: inline-block; }
}

@media (--breakpoint-large) {
    .dn-l { display: none; }
    .di-l { display: inline; }
    .db-l { display: block; }
    .dib-l { display: inline-block; }
}
